import { 
    LOGIN_REQUEST,
    LOGIN_SUCCESS,
    LOGIN_FAIL,
    REGISTER_FAIL,
    REGISTER_REQUEST,
    LOAD_USER_REQUEST,
    REGISTER_SUCCESS,
    LOAD_USER_FAIL,
    LOAD_USER_SUCCESS,
    LOGOUT_SUCCESS,
    LOGOUT_FAIL,
    CLEAR_ERRORS
} from "../constants/UserConstants"


export const authReducer =(state={user:{}},action)=>{
switch(action.type){
    case LOGIN_REQUEST:
    case REGISTER_REQUEST:
    case LOAD_USER_REQUEST:
        return{
            loading:true,
            isAuthenticated:false
        }
    case LOGIN_SUCCESS:
    case REGISTER_SUCCESS:
    case LOAD_USER_SUCCESS:
        return{
            ...state,
            loading:false,
            isAuthenticated:true,
            user: action.payload
        }
    case LOGOUT_SUCCESS:
        return{
            loading: false,
            isAuthenticated: false,
            user: null
        }
    case LOGOUT_FAIL:
        return{
            ...state,
            error: action.payload
            
        }
     case LOAD_USER_FAIL:
         return {
                loading:false,
                isAuthenticated:false,
                user: null,
                error: action.payload
         }
    case LOGIN_FAIL:
    case REGISTER_FAIL:
        return{
            ...state,
            loading:false,
            isAuthenticated:false,
           
            error: action.payload
        }
    case CLEAR_ERRORS:
        return{
           ...state,
            error:null
        }
default:
    return state
}    
}